import React from 'react'
import {Button,TabBar} from 'antd-mobile';

//返回箭头样式
const arrowStyle = {
    display: 'inline-block',
    width: '30px',
    height: '30px',
    textAlign: 'center',
    lineHeight: '30px',
    backgroundColor: 'rgba(0,0,0,0.3)',
    fontSize: '30px',
    color: 'rgba(255,255,255,0.8)',
    borderRadius: '50%',
    position: 'absolute',
    top: '3%',
    left: '5%'
}

//底部按钮
const DetailsBar =()=> {
    return (
        <div style={ {
            position: 'fixed',
            width: '100%',
            bottom: 0,
            backgroundColor:'#fff'
        } }>
            <div style={{width:'40%',display:'inline-block'}}>
                <TabBar
                    unselectedTintColor="#949494"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="shop"
                        icon={
                            <i className='iconfont icon-dianpu'></i>                           
                        }
                        data-seed="logId"
                    ></TabBar.Item>
                    <TabBar.Item
                            title="客服"
                            key="customerService"
                        icon={
                            <i className='iconfont icon-kefu'></i>                           
                        }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                            title="收藏"
                            key="collection"
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                    ></TabBar.Item>
                </TabBar>
            </div>
            <div 
            style={{
                width:'60%',
                float:'right',
                backgroundColor:'#fff',
                paddingTop:'5px',
                borderTop:'1px solid #dcdcdc',
            }}>
                <Button 
                    style={{
                        backgroundColor:'#ffa500',
                        borderRadius:0,
                        borderTopLeftRadius:'25px',
                        borderBottomLeftRadius:'25px',
                        width:'50%',
                        display:'inline-block',
                        color:'#fff5ee'
                    }}>加入购物车
                </Button>
                <Button
                    style={{
                        backgroundColor:'#ff6c00',
                        borderRadius:0,
                        borderTopRightRadius:'25px',
                        borderBottomRightRadius:'25px',
                        width:'50%',
                        display:'inline-block',
                        color:'#fff5ee'
                    }}
                >立即购买</Button>
            </div>
        </div>
    )
}

const Details = (props) => {
    return (
        <div>
            <div>
                <img
                    src='https://gw.alicdn.com/bao/uploaded/i1/668649123/O1CN01ljvhGx2HGNnw2TrKn_!!668649123.jpg_500x500q90.jpg_.webp'
                    alt="加载失败"
                    style={{ width: '100%' }}
                />
                <span style={arrowStyle}
                    onClick={()=>props.history.push('/react-work3/')}
                >&lt;</span>
            </div>
            <DetailsBar/>
        </div>
    )
}

export default Details;
