import React from 'react';
import { BrowserRouter as Router,Route,Switch,Redirect} from 'react-router-dom'
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Order from './taobao/Order';
import My from './taobao/My';
import Search from './taobao/Search';
import Details from './taobao/Details';

const Taobao = ()=>{
	return <Router>
		<Switch>
			<Route exact path='/react-work3/' component={Home} />
			<Route path='/react-work3/cart' component={Cart} />
			<Route path='/react-work3/order' component={Order} />
			<Route path='/react-work3/my' component={My} />
			<Route path='/react-work3/search' component={Search}/>
			<Route path='/react-work3/details' component={Details}/>
		</Switch>
	</Router>
}
export default Taobao;