//商品列表
import React from 'react';
import { Grid } from 'antd-mobile';
import { NavLink, withRouter } from 'react-router-dom';

//商品图片路径
const data = Array.from(new Array(12)).map((_val, i) => ({
    icon: 'https://gw.alicdn.com/bao/uploaded/i1/668649123/O1CN01ljvhGx2HGNnw2TrKn_!!668649123.jpg_500x500q90.jpg_.webp',
}));
//商品链接样式
const commodity = {
    padding: 0,
    margin: '5px',
    marginTop: 0,
    marginBottom: '-12px',
    overflow: 'hidden',
    borderRadius: '10px',
    backgroundColor: '#fff'
}
//商品图片样式
const imgStyle = {
    padding: 0,
    margin: 0,
    width: '100%'
}

const CommodityGrid = (props) => {
    return (
        <div>
            <Grid
                data={data}
                columnNum={2}
                hasLine={false}
                square={false}
                renderItem={dataItem => (
                    <div style={commodity}
                        onClick={() => { props.history.push('/react-work3/details') }}>
                        <NavLink to='/details'>
                            <img src={dataItem.icon} style={imgStyle} alt="加载失败" /></NavLink>
                        <div style={{ marginTop: '8px' }}>
                            <p style={{ color: '#000', fontSize: '16px', }}>
                                水果篮藤编收纳篮水果盘果篮客厅果盘家用馒头筐
                            </p>
                            <span style={{ color: '#f00', fontSize: '14px', }}>￥29</span>&nbsp;&nbsp;
                            <span style={{ color: '#888', fontSize: '14px', }}>434人已购买</span>
                        </div>
                    </div>
                )}
                itemStyle={{
                    background: 'rgba(0,0,0,.05)',
                    padding: 0
                }}
            />
        </div>
    )
}

export default withRouter(CommodityGrid);