import React from 'react';
import {TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Tab extends React.Component {
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#ff8c00"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye1'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/react-work3/' }
                        onPress={ () => {
                            this.props.history.push('/react-work3/');
                        } }
                        data-seed="logId"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuche2'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwuche1'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/react-work3/cart' }
                        onPress={ () => {
                            this.props.history.push('/react-work3/cart')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-icon'></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ pathname === '/react-work3/order' }
                        onPress={ () => {
                            this.props.history.push('/react-work3/order')
                        } }
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wode'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-wode1'></i>
                        }
                        title="我的淘宝"
                        key="Friend"
                        selected={ pathname === '/react-work3/my' }
                        onPress={ () => {
                            this.props.history.push('/react-work3/my')
                        } }
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Tab);