//首页搜索按钮
import React from 'react';
import {Button} from 'antd-mobile';
import {withRouter} from 'react-router-dom';

const Searchbutton =(props)=>{
    return (
        <div style={{backgroundColor:'#ff8c00'}}>
            <img style={{display:'inline-block',width:'30px',height:'30px',margin:'5px 10px 5px 10px'}}
            src='data:image/png;base64,' 
            alt='加载失败'
            />
            <Button
                    style={{ 
                        display:'inline-block',
                        width:'75%',
                        height:'30px',
                        marginBottom:'5px',
                        backgroundColor: '#ff4500', 
                        color: "#f0f8ff",
                        lineHeight:'30px',
                        borderRadius:'4px'}}
                    onClick={()=>{
                        props.history.push('/react-work3/search')
                    }}
                >
                <i className='iconfont icon-search'></i>&nbsp;
                寻找宝贝店铺
            </Button>
        </div>
    )
}

export default withRouter(Searchbutton);
