import React from 'react';
import Tab from './Tab';
import {Carousel,WingBlank} from 'antd-mobile';
import Searchbutton from './SearchButton';
import CommodityGrid from './CommodityGrid';
//轮播图图片
const imgList=[
    'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
    'https://img.alicdn.com/imgextra/i3/27/O1CN019CAYMI1C4PK6mp9qX_!!27-0-luban.jpg',
    'https://picasso.alicdn.com/imgextra/i2/382208/O1CNA11UbqJH100ff4a26c580b0b7653000k_!!382208-0-picassobanner.jpg',
    'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
    'https://img.alicdn.com/imgextra/i2/14/O1CN01FQBQC01BySAuFMDxB_!!14-0-luban.jpg',
    'https://img.alicdn.com/imgextra/i2/96/O1CN014g0ADB1Ca0eWkU60Z_!!96-0-luban.jpg'
]
//轮播图
class RotationChart extends React.Component {
    state = {
        data: ['1', '2', '3','4','5','6'],
        imgHeight: 176,
    }
    componentDidMount() {
        // simulate img loading
        setTimeout(() => {
          this.setState({
            data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI','AiyWuByWjkrrUDlFignR','AiyWjkyWklrrUDlFignR','yyqxwjkWklrrUDlFignR'],
          });
        }, 100);
    }
    render(){
        return (
            <WingBlank style={{margin:0,width:'100%'}}>
                <Carousel className="space-carousel"
                frameOverflow="hidden"
                autoplay
                infinite
                afterChange={index => this.setState({ slideIndex: index })}
                >
                {this.state.data.map((val, index) => (
                    <a
                    key={val}
                    href="http://www.alipay.com"
                    style={{
                        display: 'block',
                        position: 'relative',
                        height: this.state.imgHeight
                    }}>
                    <img
                        src={imgList[index]}
                        alt=""
                        style={{ 
                            width: '100%', 
                            verticalAlign: 'top'
                        }}
                        onLoad={() => {
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                        }}/>
                    </a>
                ))}
                </Carousel>
            </WingBlank>
        )
    }
}


class Home extends React.Component {
    
    render() {
        return (
            <div style={{marginBottom:'50px'}}>
                {/* 搜索 */}
                <Searchbutton />
                {/* 轮播图 */}
                <RotationChart />
                {/* 商品列表 */}
                <CommodityGrid />
                {/* 底部 */}
                <div style={{height:'15px'}}></div>
		        <Tab />
            </div>
        )
    }
}
export default Home;
